@media (max-width: 767px) {

	.section-pic img,
	.des-pros-tmb,
	.service-tmb,
	.fabric-teasers-tmb,
	.about-pic-pic,
	.section-catalog .catalog-form-pic,
	.anim,
	main .section-header > * {
		transform: none !important;
		opacity: 1 !important;
	}

	.section-pic img {
		margin: 0;
	}


}



.section-top h1,
.section-top .h1 {
	transform: translateY(50px);
	opacity: 0;
}

.section-top .section-sup-ttl {
	transform: translateY(50px);
	opacity: 0;
}

.section-top-info {
	transform: translateY(50px);
	opacity: 0;
}

main .section-header > * {
	transform: translateY(50px);
	opacity: 0;
}

.about-pic-pic,
.section-catalog .catalog-form-pic {
	opacity: 0;
	transform: scale(.7);
}

.col:nth-child(1) > .fabric-teasers-tmb {
	transform: translate(50px,80px) scale(.7);
	opacity: 0;
}

.col:nth-child(2) > .fabric-teasers-tmb {
	transform: translate(-50px,80px) scale(.7);
	opacity: 0;
}

.col:nth-child(3) > .fabric-teasers-tmb {
	transform: translate(0px,-80px) scale(.7);
	opacity: 0;
}

.service-tmb {
	transform: translateY(50px);
	opacity: 0;
}

main .des-pros-tmb {
	transform: translateY(50px);
	opacity: 0;
}

.section-pic img {

}

.section-pic {
	overflow: hidden;
}