.form-col-1-3 {
	flex: 0 0 33.3333%;
	max-width: 33.3333%;
}

.form-col-2-3 {
	flex: 0 0 66.6666%;
	max-width: 66.6666%;
}

.form-col-3-3 {
	flex: 0 0 100%;
	max-width: 100%;
}

.form-col-1-2 {
	flex: 0 0 50%;
	max-width: 50%;
}

.form-group {
	position: relative;
	margin: 0 0 43px 0;
	padding: 12px 0 0 0;
}

label.placeholder {
	font-family: 'GT Walsheim Pro';
	font-size: 20px;
	line-height: 1.3;
	font-weight: 300;
	color: #21306F;
	display: block;
	position: absolute;
	top: 12px;
	transition: all .3s ease;
	margin: 0;
	padding: 5px 0 13px 0;
	min-width: 100%;
}

label.placeholder.active {
	font-size: 14px;
	line-height: 16px;
	top: -4px;
	padding: 0;
	border-width: 0;
}

.card-form label.placeholder {
	padding: 0 20px;
}

.footer-subscribe-form label.placeholder {
	padding: 14px 0 0 40px;
	border-left: 2px solid transparent;
}

.footer-subscribe-form label.placeholder:not(.active) {
	color: #fff;
	font-size: 24px;
}

.footer-subscribe-form label.placeholder.active {
	color: #fff;
	padding-top: 0;
}

label.error {
	position: absolute;
	font-size: 12px;
	line-height: 1.3;
	font-weight: normal;
	color: $err-color;
	margin: 4px 0 0 0;
	padding: 0;
}

.footer-subscribe-form label.error {
	margin: 1px 0 0 42px;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
	border: none;
	border-bottom: 1px solid #21306F;
	padding: 5px 0 13px 0;
	background: none;
	transition: all .2s ease;
	font-family: 'GT Walsheim Pro';
	font-size: 20px;
	font-weight: 300;
	color: #21306F;
	width: 100%;
	display: block;
	outline: none !important;
}

.form-alt input[type=text],
.form-alt input[type=email],
.form-alt input[type=password],
.form-alt textarea {
	background-color: #fff;
	border-color: #fff;
}

.footer-subscribe-form input[type=text],
.footer-subscribe-form input[type=email],
.footer-subscribe-form input[type=password],
.footer-subscribe-form textarea {
	background: transparent;
	border: none;
	border-left: 2px solid #fff;
	color: #fff;
	font-size: 24px;
	padding: 14px 0 0 40px;
}

.card-form input[type=text],
.card-form input[type=email],
.card-form input[type=password],
.card-form textarea {
	border: none;
	background: none;
	font-size: 26px;
	padding: 12px 20px 9px 20px;
}

textarea {
	resize: none;
}

input[type=text].error,
input[type=email].error,
input[type=password].error,
textarea.error {
	border-color: $err-color;
	color: $err-color;
}

.order-form .form-footer .btn {
	width: 100%;
}

.feedback-form .form-footer .btn {
	min-width: 285px;
}

.form-col-name {
	flex: 0 0 58.4%;
	max-width: 58.4%;
}

.form-col-phone {
	flex: 0 0 41.6%;
	max-width: 41.6%;
}

.form-agree {
	font-size: 14px;
	line-height: 1.3;
}

.form-agree a,
.form-agree a:focus {
	color: #d9c3ae;
	text-decoration: none;
}

.form-agree a:hover {
	text-decoration: underline;
}

.project-form .form-agree {
	margin-top: -4px;
}

.form-checkboxes,
.form-radios {
	display: flex;
	margin: 0 -15px;
	flex-wrap: wrap;
}

.form-checkbox,
.form-radio {
	flex: 0 0 100%;
	max-width: 100%;
	padding: 0 15px;
	margin: 0 0 15px 0;
}

.form-radio-narrow,
.form-checkbox-narrow {
	flex: 0 0 50%;
	max-width: 50%;
}

input[type=checkbox],
input[type=radio] {
	position: absolute;
	visibility: hidden;
}

input[type=checkbox] + label,
input[type=radio] + label {
	position: relative;
	padding: 0 0 0 50px;
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	position: relative;
	margin: 0;
	cursor: pointer;
	display: flex;
}

input[type=radio] + label:before {
	content: "";
	width: 25px;
	height: 25px;
	position: absolute;
	left: 0;
	top: 0;
	background-color: #fff;
	transition: all .1s ease;
	background-image: url(../images/radio.svg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 0 0;
	border: 1px solid #2C57AB;
	border-radius: 50%;
}

input[type=radio]:checked + label:before {
	background-size: 13px 13px;
}

input[type=checkbox] + label:before {
	content: "";
	width: 25px;
	height: 25px;
	position: absolute;
	left: 0;
	top: 0;
	background-color: #fff;
	transition: all .1s ease;
	background-image: url(../images/checkbox.svg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 0 0;
	border: 1px solid #2C57AB;
}

input[type=checkbox]:checked + label:before {
	background-size: 13px 13px;
}

.form-checkboxes-small input[type=checkbox] + label {
	font-size: 14px;
}

.form-checkboxes-small input[type=checkbox] + label:before {
	top: -2px;
}

.form-checkboxes-small {
	margin-bottom: 25px;
}

.form-footer {
	margin: 0;
}

.modal-form .form-footer {
	margin: 30px 0 0 0;
}

.form-footer .btn {
	width: 100%;
}

.project-form .form-footer {
	margin-top: 4px;
}

.project-form .form-footer .form-file {
	padding-top: 9px;
	margin-bottom: 18px;
}

.form-footer .row {

}

.team-form-col {
	flex: 0 0 33.3333%;
	max-width: 33.3333%;
}

.team-form .form-footer {
	margin: 20px 0 0 0;
}

.team-form .form-agree {
	font-weight: normal;
	font-size: 16px;
	line-height: 130%;
	color: #fff;
}

.form-file {
	text-align: center;
	margin: 0 0 42px 0;
	min-height: 37px;
}

.form-file .file-caption {
	-ms-flex-order: 2 !important;
	order: 2 !important;
	width: auto !important;
	height: auto !important;
	padding: 0 !important;
	margin: 0 !important;
	border: none !important;
	outline: none !important;
	display: none;
	background: none !important;
	border-radius: 0 !important;
	-ms-flex-preferred-size: 100%;
	flex-basis: 100%;
	box-shadow: none !important;
}

.file-caption.icon-visible {
	display: block;
	overflow: hidden;
}

.form-file .file-caption-name {
	font-weight: normal;
	font-size: 16px;
	line-height: 1.5;
	color: #D9C3AE;
	text-align: left !important;
	padding: 0 !important;
	margin: 22px 0 0 41px;
}

.form-file .input-group-append {
	-ms-flex-pack: center !important;
	justify-content: center !important;
	height: 22px !important;
}

.form-file .btn-file,
.form-file .btn-file:focus {
	font-weight: normal;
	font-size: 0 !important;
	line-height: 34px !important;
	height: 37px !important;
	/* or 260% */
	text-align: center;
	color: #D9C3AE !important;
	padding: 1px 0 0 41px !important;
	background: none !important;
	border-radius: 0 !important;
	cursor: pointer;
	display: block;
	position: relative;
	transition: all .15s ease;
	box-shadow: none !important;
}

.form-file .btn-file span {
	font-weight: normal;
	font-size: 18px;
	line-height: 1.5;
}

.form-file .btn-file:hover {
	color: #fff !important;
}

.form-file .btn-file:before {
	content: "";
	width: 24px;
	height: 27px;
	background: url(../images/ico-file.svg) 0 0 no-repeat;
	background-size: 100% auto;
	position: absolute;
	left: 0;
	top: 0;
	transition: all .15s ease;
	pointer-events: none;
}

.form-file .btn-file:hover:before {
	background: url(../images/ico-file-hover.svg) 0 0 no-repeat;
}

.fileinput-remove {
	display: none !important;
	position: absolute !important;
	top: 100%;
	left: 3px;
	padding: 0 !important;
	margin: 26px 0 0 0 !important;
	font-size: 0 !important;
	line-height: 0 !important;
	width: 17px !important;
	height: 16px !important;
	box-shadow: none !important;
	background-color: transparent !important;
}

.fileinput-remove:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/remove-file.svg) center center no-repeat;
	transition: all .2s ease;
	opacity: 1;
}

.fileinput-remove:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/remove-file-hover.svg) center center no-repeat;
	transition: all .2s ease;
	opacity: 0;
}

.fileinput-remove:hover:before {
	opacity: 0;
}

.fileinput-remove:hover:after {
	opacity: 1;
}

.fileinput-remove i {
	display: none !important;
}

.form-file .input-group-btn {
	position: relative;
	font-size: 14px !important;
	line-height: 22px !important;
	margin: 0 !important;
	outline: none !important;
}

.file-caption.icon-visible + .input-group-btn .fileinput-remove {
	display: block !important;
}

.file-caption + .input-group-btn {
	display: block !important;
}

.btn-file i {
	display: none !important;
}

.btn-file .kv-upload-progress {
	display: none !important;
}

.form-group-file.form-group-error:after {
	content: "Выберите файл";
	font-size: 11px;
	line-height: 15px;
	text-align: right;
	margin: 13px 0 0 0;
	color: #FFfff6;
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
}

.form-group-file label.error {
	display: none !important;
}

.footer-subscribe-form {
	display: flex;
	flex-wrap: nowrap;
}

.footer-subscribe-field {
	flex-grow: 1;
	margin: 0 30px 0 0;
}

.footer-subscribe-form input[type=submit] {
	background: transparent url(../images/footer-subscribe-submit.svg) 0 0 no-repeat;
	width: 50px;
	height: 51px;
	border: none;
	-webkit-appearance: none;
	font-size: 0;
	line-height: 0;
	color: transparent;
	display: block;
	cursor: pointer;
	margin-top: -1px;
}

.footer-subscribe-form .form-group {
	margin: 0;
}

.card-form {
	display: flex;
	background: #fff;
	border-radius: 40px;
	padding: 10px;
}

.card-form .form-group {
	margin: 0;
}

.card-field {
	flex-grow: 1;
}

.card-button {
	flex-grow: 0;
}

.card-button .btn {
	min-width: 255px;
}

.card-form-agree {
	font-size: 14px;
	line-height: 1.5;
}

.card-form-agree a,
.card-form-agree a:focus {
	text-decoration: underline;
	color: #fff;
}

.label-pic {
	margin: -1px 15px 6px -10px;
}

// Bootstrap select

.bootstrap-select.btn-group {
	display: block !important;
}

.bootstrap-select > .dropdown-toggle,
.bootstrap-select > .dropdown-toggle:focus {
	display: block;
	width: 100%;
	background: transparent;
	border: none;
	border-bottom: 1px solid #21306F;
	-webkit-appearance: none;
	font-weight: normal;
	color: #21306F;
	padding: 5px 40px 12px 0;
	outline: none !important;
	font-size: 0;
	line-height: 0;
}

.bootstrap-select.btn-group-error > .dropdown-toggle,
.bootstrap-select.btn-group-error > .dropdown-toggle:focus {
	border-bottom-color: $err-color;
}

.bootstrap-select.show > .dropdown-toggle,
.bootstrap-select.show > .dropdown-toggle:focus {
	//border-bottom-color: #479BFF;
}

.bootstrap-select > .dropdown-toggle.bs-placeholder, .bootstrap-select > .dropdown-toggle.bs-placeholder:active, .bootstrap-select > .dropdown-toggle.bs-placeholder:focus, .bootstrap-select > .dropdown-toggle.bs-placeholder:hover {
	color: #21306F;
}

.bootstrap-select.btn-group .dropdown-toggle .filter-option {
	font-weight: normal;
	font-size: 20px;
	line-height: 1.3;
	color: #21306F;
}

.bootstrap-select.btn-group .dropdown-toggle.bs-placeholder .filter-option {
	color: transparent;
}

.bootstrap-select.btn-group .dropdown-toggle .caret {
	width: 13px;
	height: 9px;
	background: none;
	position: absolute;
	right: 0;
	top: 15px;
	margin: 0;
	display: block;
	vertical-align: top;
}

.bootstrap-select.btn-group .dropdown-toggle .caret:before {
	content: "";
	display: block !important;
	width: 13px;
	height: 8px;
	background: url(../images/select-arrow.svg) 0 0 no-repeat;
	background-size: 13px auto;
	position: absolute;
	left: 0;
	top: 0;
	transition: all .15s ease;
	opacity: 1;
}

.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
	width: 100%;
}

.bootstrap-select.btn-group .dropdown-menu {
	margin: 0 0 0 0;
	padding: 0 0;
	border-radius: 0;
	border: none;
	box-shadow: 0px 4px 55px rgba(0, 0, 0, 0.1);
}

.bootstrap-select.btn-group .dropdown-menu.inner {
	display: block;
}

.bootstrap-select.btn-group .dropdown-menu li {
	padding: 0 !important;
	margin: 0 !important;
	font-size: 16px;
	line-height: 1.5;
	font-weight: normal;
}

.bootstrap-select.btn-group .dropdown-menu li.disabled {
	display: none;
}

.bootstrap-select.btn-group .dropdown-menu li:before {
	display: none !important;
}

.bootstrap-select.btn-group .dropdown-menu li a {
	display: block;
	padding: 12px 17px 12px 20px;
	color: #21306F;
	position: relative;
	outline: none !important;
}

.bootstrap-select.btn-group .dropdown-menu li a:after {
	content: "";
	background: url(../images/select-check.svg) 0 0 no-repeat;
	width: 10px;
	height: 8px;
	position: absolute;
	right: 20px;
	top: 20px;
	opacity: 0;
}

.bootstrap-select.btn-group .dropdown-menu li a:hover {
	background: rgba(44, 87, 171, 0.1);
	color: #21306F;
}

.bootstrap-select.btn-group .dropdown-menu li.selected a {
	background: rgba(44, 87, 171, 0.1);
}

.bootstrap-select.btn-group .dropdown-menu li.selected a:after {
	opacity: 1;
}

div.dropdown-menu {
	z-index: 1000;
}

// Bootstrap select END

