@import "vars";

@charset "UTF-8";

// Fonts

@font-face {
	font-family: 'Cormorant';
	src: url("../fonts/subset-Cormorant-Regular.woff2") format("woff2"), url("../fonts/subset-Cormorant-Regular.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'GT Walsheim Pro';
	src: url('../fonts/subset-GTWalsheimPro-Medium.woff2') format('woff2'),
	url('../fonts/subset-GTWalsheimPro-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'GT Walsheim Pro';
	src: url('../fonts/subset-GTWalsheimPro-Light.woff2') format('woff2'),
	url('../fonts/subset-GTWalsheimPro-Light.woff') format('woff');
	font-weight: 300;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'GT Walsheim Pro';
	src: url('../fonts/subset-GTWalsheimPro-Bold.woff2') format('woff2'),
	url('../fonts/subset-GTWalsheimPro-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'GT Walsheim Pro';
	src: url('../fonts/subset-GTWalsheimPro-Regular.woff2') format('woff2'),
	url('../fonts/subset-GTWalsheimPro-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}



// Fonts END

* {
	outline: none !important;
	min-height: 0;
	min-width: 0;
}

html,
body {
	font-family: 'GT Walsheim Pro', sans-serif;
	font-size: 20px;
	line-height: 1.3;
	font-weight: 300;
	color: $text-color;
	min-width: 360px;
}

.red {
	color: #E44B4B;
}

h1,
.h1 {
	font-family: 'Cormorant';
	font-size: 80px;
	line-height: 90%;
	font-weight: normal;
	letter-spacing: -0.05em;
	text-transform: uppercase;
	color: #FFFFFF;
	margin: 0;
}

h2,
.h2 {
	font-family: 'Cormorant';
	font-size: 80px;
	line-height: 90%;
	font-weight: normal;
	letter-spacing: -0.05em;
	text-transform: uppercase;
	color: $blue-color;
	margin: 0;
}

h3,
.h3 {
	font-family: 'Cormorant';
	font-size: 50px;
	line-height: 100%;
	font-weight: normal;
	letter-spacing: -0.05em;
	text-transform: uppercase;
	color: #D1B2AE;
	margin: 81px 0 20px 0px;
}

h1:first-child,
.h1:first-child,
h2:first-child,
.h2:first-child,
h3:first-child,
.h3:first-child,
h4:first-child,
.h4:first-child {
	margin-top: 0 !important;
}

main a,
main a:focus {
	color: $blue-color;
	text-decoration: none;
	transition: all .15s ease;
}

main a:hover {

}

p {
	margin: 0 0 15px 0;
}

p:last-child {
	margin-bottom: 0;
}

strong {
	font-weight: bold !important;
}

main ul {
	margin: 0 0 15px 33px;
	padding: 0;
	list-style: none;
}

main ul > li {
	padding: 0 0 0 35px;
	margin: 0 0 5px 0;
	position: relative;
}

main ul > li:before {
	content: "";
	width: 15px;
	height: 15px;
	background: url(../images/li-bullet.svg) 0 0 no-repeat;
	position: absolute;
	left: 0;
	top: 7px;
}

main ol,
.modal ol {
	padding: 0;
	margin: 0 0 15px 0;
	counter-reset: item;
	list-style: none;
}

main ol > li,
.modal ol > li {
	position: relative;
	padding: 0 0 0 45px;
	margin: 0 0 15px 0;
}

main ol > li:before,
.modal ol > li:before {
	color: #FDB930;
	font-family: 'GT Walsheim Pro';
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
	/* identical to box height */
	position: absolute;
	left: 0;
	top: -1px;
	content: "0" counter(item) " ";
	counter-increment: item;
	letter-spacing: .1em;
	color: transparent;
	-webkit-text-fill-color: transparent;
	/* Will override color (regardless of order) */
	-webkit-text-stroke-width: 1px;
	-webkit-text-stroke-color: #6E9DB4;
}

main ol > li:nth-child(n+10):before {
	content: counter(item) " ";
}

main ol > li:after {
	display: block;
	content: "";
	width: 30px;
	height: 4px;
	background: url(../images/ol-deco.svg) 0 0 no-repeat;
	position: absolute;
	left: 34px;
	top: 11px;
}

main ol.ol-alt > li {
	padding-left: 40px;
}

main ol.ol-alt > li:before {
	color: #EC5F65;
}

main ol.ol-alt > li:after {
	display: none;
}

.ol-red {
	font-weight: 600;
	color: #EC5F65;
}

main table {
	width: 100%;
	font-family: 'GT Walsheim Pro';
	font-size: 16px;
	line-height: 26px;
	color: #272E33;
}

main table th {
	border-top: 2px solid #FDB930;
	border-bottom: 2px solid #FDB930;
	font-family: 'GT Walsheim Pro';
	font-weight: 500;
	font-size: 18px;
	line-height: 26px;
	color: #000000;
	vertical-align: top;
	padding: 20px 15px 20px 15px;
}

main table td {
	border-bottom: 1px solid #C4C4C4;
	vertical-align: top;
	padding: 22px 15px 22px 15px;
}

main table th:first-child,
main table td:first-child {
	padding-left: 0;
}

main table th:last-child,
main table td:last-child {
	padding-right: 0;
}

/* Buttons */
.btn,
.btn:focus {
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	border: none;
	-webkit-appearance: none;
}

.btn:active {
	box-shadow: none;
}

.btn-fwd,
.btn-fwd:focus {
	font-size: 16px;
	line-height: 24px;
	font-weight: bold;
	text-transform: uppercase;
	padding: 0 0 0 45px;
	position: relative;
	color: #6E9DB4;
}

.btn-fwd:hover {
	color: #FF6F53;
}

.btn-fwd:before {
	content: "";
	width: 28px;
	height: 51px;
	background: url(../images/ico-btn-fwd.png) 0 0 no-repeat;
	background-size: 28px auto;
	position: absolute;
	left: 0;
	top: -11px;
}

.btn-fwd:hover:before {
	background-position: 0 -51px;
}

.btn-1,
.btn-1:focus {
	font-weight: normal;
	font-size: 20px;
	line-height: 1.3;
	text-align: center;
	color: #fff;
	transition: all .25s ease;
	padding: 20px 30px 19px 30px;
	background: #21306F;
	border-radius: 33px;
}

.btn-1:hover {
	background: #d0b8b5;
}

.btn-1-med,
.btn-1-med:focus {
	padding: 13px 25px 16px 25px;
}

.btn-1-small,
.btn-1-small:focus {
	font-size: 18px;
	padding: 19px 15px 19px 15px;
}

.btn-2,
.btn-2:focus {
	font-weight: 500;
	font-size: 18px;
	text-align: center;
	color: #fff;
	transition: all .25s ease;
	padding: 16px 30px 15px 30px;
	background: transparent;
	border: 1px solid #E44B4B;
}

.btn-1:hover,
.btn-2:hover {
	color: #fff !important;
}

.btn-3,
.btn-3:focus {
	font-weight: 500;
	font-size: 18px;
	line-height: 26px;
	text-align: center;
	text-align: center;
	color: #fff;
	transition: all .25s ease;
	padding: 16px 29px 16px 29px;
	background: transparent;
	border-radius: 30px;
	border: 1px solid #6E9DB4;
}

.btn-3:hover {
	color: #FF6F53;
	border-color: #6E9DB4;
}

.btn-3:active {
	color: #FF6F53;
	border-color: #6E9DB4;
}

.btn-4,
.btn-4:focus {
	font-weight: 500;
	font-size: 18px;
	line-height: 26px;
	text-align: center;
	text-align: center;
	color: #fff;
	transition: all .25s ease;
	padding: 17px 30px 17px 30px;
	background: #6E9DB4;
	border-radius: 30px;
}

.btn-4:hover {
	color: #fff;
	background: #6E9DB4;
	border-color: #6E9DB4;
}

.btn-4:active {
	color: #fff;
}

.expandable-trigger-wrapper {
	margin: 48px 0 0 0;
}

.expandable-trigger,
.expandable-trigger:focus {
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	text-transform: uppercase;
	padding: 0 66px 0 0;
	position: relative;
	color: #6E9DB4;
	text-decoration: none !important;
	transition: all .15s ease;
	display: inline-block;
}

.expandable-trigger:hover {
	color: #FF6F53;
}

.expandable-trigger-arrow {
	position: absolute;
	right: 0;
	top: -11px;
	width: 45px;
	height: 45px;
}

.expandable-trigger-arrow:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/expandable-trigger-ico.svg) 0 0 no-repeat;
	transition: all .15s ease;
}

.expandable-trigger-arrow:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/expandable-trigger-ico-hover.svg) 0 0 no-repeat;
	transition: all .15s ease;
	opacity: 0;
}

.expandable-trigger:hover .expandable-trigger-arrow:before {
	opacity: 0;
}

.expandable-trigger:hover .expandable-trigger-arrow:after {
	opacity: 1;
}

.expandable-trigger.active .expandable-trigger-arrow:before,
.expandable-trigger.active .expandable-trigger-arrow:after {
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.expandable-trigger-text {
	display: inline-block;
}

.btn-back,
.btn-back:focus {
	width: 60px;
	height: 60px;
	position: relative;
	border: 1px solid #FF6F53;
	border-radius: 50%;
	-ms-flex-positive: 0;
	flex-grow: 0;
	-ms-flex-negative: 0;
	flex-shrink: 0;
}

.btn-back:after {
	content: "";
	width: 8px;
	height: 14px;
	background: url(../images/ico-btn-back.svg) 0 0 no-repeat;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	transform: translateX(-50%) translateY(-50%);
}

.btn-order {
	background: #FF6F53;
	border-radius: 0px 0px 90px 0px;
	width: 100%;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	letter-spacing: -0.03em;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 27px 15px 49px 33px;
	white-space: normal;
	text-align: left;
	display: block;
	position: relative;
}

.btn-order:after {
	content: "";
	position: absolute;
	width: 128px;
	height: 43px;
	background: url(../images/btn-order-arrow.svg) 0 0 no-repeat;
	position: absolute;
	left: 33px;
	bottom: 6px;
}

.btn-order:hover {
	background: #EA5134;
}

/* Buttons END */

.slider-wrapper {
	margin: 0 -15px;
	position: relative;
}

.slick-slide {
	padding: 0 15px;
}

.slick-arrow {
	background: none;
	border: none;
	display: block;
	-webkit-appearance: none;
	width: 52px;
	height: 52px;
	position: absolute;
	top: 50%;
	margin-top: -26px;
	cursor: pointer;
	font-size: 0;
	transition: all .3s ease;
	padding: 0;
	z-index: 200;
}

.slick-arrow:before {
	content: "";
	position: absolute;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background: #21306f;
	top: 50%;
	margin-top: -30px;
	transition: all .2s ease;
	opacity: 0;
}

.slick-prev:before {
	left: -20px;
}

.slick-next:before {
	right: -20px;
}

.slick-arrow:hover:before {
	opacity: 1;
}

.slick-arrow.slick-disabled {
	opacity: 0.3 !important;
	cursor: default;
}

.slick-arrow.slick-disabled:before {
	opacity: 0 !important;
}

.slick-vertical .slick-arrow {
	width: 43px;
	height: 67px;
}

.slick-arrow:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-repeat: no-repeat;
	transition: all .2s ease;
	z-index: 10;
	background-position: center center;
}

.slick-prev:after {
	background-image: url(../images/slick-prev.svg);
}

.slick-next:after {
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	left: -20px;
}

.slick-next {
	right: -20px;
}

.slider-count {
	font-size: 20px;
	line-height: 30px;
	font-weight: 300;
	position: absolute;
	left: 114px;
	bottom: -65px;
}

.slider-count-cur {
	font-weight: bold;
}

.slider-count-sep {
	padding: 0 6px;
}

.photo-slider .slick-prev {
	top: auto;
	margin-top: 0;
	bottom: -73px;
	left: 15px;
}

.photo-slider .slick-next {
	right: auto;
	top: auto;
	margin-top: 0;
	bottom: -73px;
	left: 205px;
}

.photo-slider-wrapper {
	margin-bottom: 167px;
}

/* Modals */
.modal-backdrop {
	background: #21306F;
}

.modal-backdrop.show {
	opacity: .9;
}

.modal-dialog {
	pointer-events: auto;
}

.side-modal {
	padding-right: 0 !important;
}

.side-modal .modal-dialog {
	margin: 0 auto 0 0;
	transform: translate(-100%, 0) !important;
	max-width: 100%;
	height: 100%;
}

.side-modal.show .modal-dialog {
	transform: translate(0, 0) !important;
}

.side-modal .modal-content {
	border: none;
	border-radius: 0;
	background: none;
	height: 100%;
	padding: 0 90px 0 0;
}

.side-modal .close {
	position: absolute;
	right: 29px;
	top: 35px;
	cursor: pointer;
	z-index: 100;
	opacity: 1 !important;
	transition: all .15s ease;
}

.modal .close svg * {
	transition: all .15s ease;
}

.modal .close:hover svg *[stroke] {
	stroke: #E44B4B;
}

.modal .close:hover svg *[fill] {
	fill: #E44B4B;
}

.side-modal .modal-content .row {
	-ms-flex-wrap: nowrap;
	flex-wrap: nowrap;
	min-height: 100%;
}

.side-modal-r {
	padding: 0 15px;
	flex: 0 0 280px;
	max-width: 280px;
	overflow: hidden;
}

.side-modal-l {
	padding: 0 15px;
	min-height: 100%;
	max-width: 1020px;
	flex-grow: 1;
}

.side-modal-l-inner {
	background: #fff;
	padding: 0;
	min-height: 100%;
}

.form-modal {
	padding: 90px !important;
	color: #fff;
	text-align: center;
	font-weight: normal;
}

.form-modal .modal-header {
	border: none;
	padding: 0;
	margin: 0 0 16px 0;
}

.form-modal .modal-body {
	padding: 0;
}

.form-modal .modal-dialog {
	max-width: 540px;
	height: auto;
	min-height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	-ms-flex-pack: center;
	justify-content: center;
	margin: 0 auto;
}

.form-modal .modal-header h2,
.form-modal .modal-header .h2 {
