	color: #D0B8B5;
	margin: 0 0 21px 0;
}

.form-modal-pic {
	margin: 0 0 -93px 0;
}

.form-modal-pic img {
	width: 100%;
}

.hello-modal {
	font-weight: 500;
	font-size: 18px;
	line-height: 150%;
}

.hello-modal .modal-dialog {
	max-width: 920px;
}

.hello-modal-pic {
	position: absolute;
