/* Modals END */


.over-wrapper {
	overflow: hidden;
}



.modal-open .over-wrapper {
	overflow: hidden;
}

header {
	padding: 0 0 0 0;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 200;
	transition: all .3s ease;
	font-weight: normal;
}

.section {
	position: relative;
	padding: 52px 0 52px 0;
}

.section-top {
	padding: 150px 0 50px 0;
	min-height: 750px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.section-top-video {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}

.section-top-video:after {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	width: 57%;
	background: linear-gradient(to right, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}

ul.navbar-nav {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	margin: 0 -10px -5px -10px;
	padding: 0;
	font-size: 16px;
	letter-spacing: 0.02em;
	text-transform: uppercase;
}

ul.navbar-nav > li {
	padding: 0;
	margin: 0 10px 5px 10px;
}

ul.navbar-nav a,
ul.navbar-nav a:focus {
	color: #fff;
	text-decoration: none;
	transition: all .15s ease;
}

ul.navbar-nav a.active {
	color: #D8B9A6 !important;
}

.header-fixed ul.navbar-nav a,
.header-fixed ul.navbar-nav a:focus {
	color: #21306F;
}

.header-col-1 {
	flex: 0 0 40%;
	max-width: 40%;
	padding-top: 30px;
	padding-bottom: 19px;
}

.header-fixed .header-col-1 {
