	background: url(../images/v-lines.svg) 0 0 no-repeat;
	width: 13px;
	background-size: 100% 100%;
	position: absolute;
	left: 0;
	top: 7px;
	bottom: 6px;
}

.contacts-form {
	max-width: 444px;
	margin: 60px 0 0 auto;
	position: relative;
}

.contacts-form:before {
	content: "";
	width: 650px;
	height: 650px;
	border-radius: 50%;
	border: 1px dashed #D8B9A6;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

.contacts-form-inner {
	position: relative;
}

.contacts-form h3,
.contacts-form .h3 {
	text-align: center;
	margin: 0 0 26px 0;
}

.footer-bottom {
	text-align: center;
	font-weight: normal;
	font-size: 14px;
	line-height: 16px;
	/* identical to box height */

	text-align: center;
	letter-spacing: 0.02em;
	text-transform: uppercase;

	color: #21306F;
	margin: 144px 0 0 0;
}

.footer-dev a,
.footer-dev a:focus {
	color: #21306F;
	text-decoration: none !important;

}

.footer-dev a span {
	color: #D8B9A6;
}

.footer-copy {
	margin: 0 0 14px 0;
}

.header-fixed {
	background: #fff;
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
}

.header-fixed .header-logo {
	height: 70px;
	overflow: hidden;
	-ms-flex-align: end;
	align-items: flex-start;
	margin: 0 0 0 0;
}

.fabric-modal .section-header h2,
.fabric-modal .section-header .h2 {
}

.fabric-modal .section-info {
}

.fabric-item-text {
	font-size: 20px;
	line-height: 1.3;
	margin: 37px 0 0 0;
}

.fabric-vars {
	margin: 50px 0 0 0;
}

.vars-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px -30px -15px;
}

.vars-item {
	flex: 0 0 16.666%;
	max-width: 16.666%;
	padding: 0 15px;
	margin: 0 0 30px 0;
}

.vars-item img {
	width: 100%;
	border-radius: 50%;
}

.prod-modal .catalog-form-wrapper {
	margin: 160px 0 0 0;
}

.prod-item-slider-wrapper {
	position: absolute;
	left: 50%;
	margin-left: 15px;
	right: 0;
	top: 0;
}

.prod-item-slider .slick-slide {
	padding: 0;
}

.prod-item-slider img {
	width: 100%;
}

.prod-col {
	flex: 0 0 50%;
	max-width: 50%;
}

.prod-item-top {
	position: relative;
	padding: 88px 0 0 0;
}

.prod-item-descr {
	padding: 0 10px 0 0;
}

.prod-item-text {
	margin: 38px 0 0 0;
}

.prod-item-pros-wrapper {
	margin: 39px 0 0 0;
}

.prod-item-section h2,
.prod-item-section .h2 {
	font-weight: normal;
	font-size: 40px;
	line-height: 100%;
	/* identical to box height, or 40px */

	letter-spacing: -0.05em;
	text-transform: uppercase;

	color: #21306F;
	margin: 0 0 43px 0;
}

.prod-item-section-fabrics {
	background: url(../images/prod-fabrics-bg.jpg) 0 0 no-repeat;
	background-size: 100% auto;
	padding: 186px 0 0 0;
}

.prod-item-section-cases {
	margin: 92px 0 204px 0;
}

.prod-modal-content {
	padding: 0 0 92px 0;
	background: url(../images/prod-modal-content-bg.jpg) 0 bottom no-repeat;
	background-size: 100% auto;
}

.prod-item-slider .slick-prev {
	left: 50px;
	top: auto;
	bottom: 37px;
}

.prod-item-slider .slick-next {
	right: auto;
	left: 140px;
	top: auto;
	bottom: 37px;
}

.ov-h {
	overflow: hidden;
	overflow-x: auto;
}

.ov-x {
	//overflow-x: auto;
}

.section-products-wrapper {
	position: relative;
}

@media (min-width: 768px) {

	main .section {
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	main .section-pic {
		min-height: 0;
	}

	main .section-products,
	main .section-catalog,
	main .section-contacts {
		min-height: 0;
	}

	ul.navbar-nav a:hover {
		color: #D8B9A6 !important;
	}

}

.form-modal-lead {
	margin: 0 0 30px 0;
}

.cat-form-wrapper {
	margin: 64px 0 0 0;
}

.cat-form-wrapper h3,
.cat-form-wrapper .h3 {
	font-size: 40px;
	line-height: 100%;
	/* identical to box height, or 40px */

	letter-spacing: -0.05em;
	text-transform: uppercase;

	color: #21306F;
	margin: 0 0 29px 0;
}

.cat-form {
	max-width: 500px;
}

.cat-form-col-1 {
	flex: 0 0 47%;
	max-width: 47%;
}

.cat-form-col-2 {
	flex: 0 0 53%;
	max-width: 53%;
}

.cat-form .form-footer {
	margin: 2px 0 0 0;
}

.cat-form .form-footer .btn {
	width: 100%;
}

.cat-form .form-agree {
	color: #21306F;
}

.cat-form .form-group {
	margin: 0 0 30px 0;
}

.fixed {
	position: fixed;
}

.form-modal-lead {
	font-size: 24px;
	line-height: 1.3;
}

.prod-modal .close {
	border-color: #fff;
	background: #fff;
}

.fabric-modal .catalog-form-wrapper {
	margin: 0;
	padding: 167px 62px 107px 62px;
	border-radius: 270px;
}

.fabric-modal .catalog-form-pic {
	width: 169px;
	left: 50%;
	top: -30px;
	margin-left: -90px;
}

.fabric-modal .catalog-form-wrapper h2,
.fabric-modal .catalog-form-wrapper .h2 {
	font-size: 40px;
	margin: 0 0 28px 0;
}

.fabric-modal .catalog-form-col-narrow {
	flex: 0 0 100%;
	max-width: 100%;
}

.fabric-modal .catalog-form {
	margin: 0;
}

.fabric-modal .catalog-form .form-group {
	margin: 0 0 43px 0;
}

.fabric-modal .catalog-form .form-agree {
	margin: -18px 0 0 0;
}

.fabric-modal .catalog-form .form-footer {
	margin: 26px 0 0 0;
}